import React, { useState, useEffect } from 'react';

const Sidebar: React.FC = () => {
  const [bridges, setBridges] = useState([]);

  useEffect(() => {
    fetch('/api/bridges') // Fetch bridge data from the API
      .then((res) => res.json())
      .then((data) => setBridges(data.bridges))
      .catch((err) => console.error(err));
  }, []);

  return (
    <div className="h-full p-4 overflow-y-auto">
      <h2 className="text-xl font-bold mb-4">Bridge List</h2>
      {bridges.length > 0 ? (
        <ul>
          {bridges.map((bridge: any) => (
            <li
              key={bridge.id}
              className="p-2 mb-2 bg-gray-100 hover:bg-gray-200 rounded-md cursor-pointer"
            >
              <h3 className="font-medium">{bridge.structureNumber}</h3>
              <p className="text-sm text-gray-600">
                Built in {bridge.yearBuilt}
              </p>
            </li>
          ))}
        </ul>
      ) : (
        <p>Loading bridges...</p>
      )}
    </div>
  );
};

export default Sidebar;
